import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {SupervisorsComponent} from '../supervisors/supervisors/supervisors.component';
import {AddSuperComponent} from '../supervisors/add-super/add-super.component';
import {UpdateSuperComponent} from '../supervisors/update-super/update-super.component';
import {ChangeRoleSuperComponent} from '../supervisors/change-role-super/change-role-super.component';
import {DeleteSuperComponent} from '../supervisors/delete-super/delete-super.component';
import {ViewRoleComponent} from './view-role/view-role.component';
import {RolesComponent} from './roles.component';
import {UpdateRoleComponent} from './update-role/update-role.component';
import {ChangePermationsComponent} from './change-permations/change-permations.component';
import {DeleteRoleComponent} from './delete-role/delete-role.component';

const routes: Routes = [
    {path: '', component: RolesComponent},
    {path: 'add-role', component: ViewRoleComponent},
    {path: 'update-role/:id', component: UpdateRoleComponent},
    {path: 'change-permeation-role', component: ChangePermationsComponent},
    {path: 'delete-role', component: DeleteRoleComponent}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class RolesRoutingModule {
}
