import {Component, OnInit} from '@angular/core';
import {AppAdminService} from '../../Services/app-admin.service';
import {IAppAdmin, IpermationDetail} from '../../Models/app-admin';
import {DeleteRoleComponent} from './delete-role/delete-role.component';
import {ToastaService, ToastOptions} from 'ngx-toasta';
import {MatDialog} from '@angular/material/dialog';

@Component({
    selector: 'app-roles',
    templateUrl: './roles.component.html',
    styleUrls: ['./roles.component.css']
})
export class RolesComponent implements OnInit {
    cols: any;
    products: any;
    roles: IAppAdmin[];
    rolesPermations: any[];
    display: boolean = false;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'id', header: 'ID'},
            {field: 'name', header: 'Name'},
            {field: 'display_name', header: 'Display'},
            {field: 'description', header: 'Description'}
        ];
        this.getRolesApp();
    }

    showDialog(data: any) {
        this.rolesPermations = data.permissions;
        console.log(data.permissions);
        this.display = true;
    }

    getRolesApp() {
        this._AppAdminService.getRoles().subscribe((res: { data }) => {
            this.roles = res.data.all as IAppAdmin[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }

    openRoleDeleteDialog(id) {
        const dialogRef = this.dialog.open(DeleteRoleComponent, {
            data: {id}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteMember(result);

            }
        });
    }

    onDeleteMember(roleId) {
        this._AppAdminService.deleteRole(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getRolesApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }
}
